//--|ðŸ Š Form.verify.scss ðŸ ˆ|--//
@use '../../../designs/Palettes.scss';

@mixin desktop-landscape {
  //--|ðŸ Š display: grid; ðŸ ˆ|--//
  grid-template: 1fr auto 1fr / 1fr; //--|ðŸ Š rows / columns ðŸ ˆ|--//
  div[class*='header'] {
    display: inherit; //--|ðŸ Š display: grid; ðŸ ˆ|--//
    grid-area: 1 / 1 / 2 / 2; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
    grid-template: 4rem 1fr / 4rem 1fr 4rem; //--|ðŸ Š rows / columns ðŸ ˆ|--//
    button[class*='close'] {
      background: none;
      grid-area: 1 / 3 / 2 / 4; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      img {
        width: 3rem;
        height: 3rem;
        padding: 0.5rem;
      }
    }
    div[class*='text'] {
      display: flex;
      align-items: center;
      grid-area: 1 / 2 / 3 / 3;
      filter: drop-shadow(black 0 0 8px); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      h4 {
        width: 100%;
        padding: 4px;
        line-clamp: 3; //--|ðŸ Š Standard property ðŸ ˆ|--//
        color: white;
        text-wrap: balance;
        text-align: center; //--|ðŸ Š Justifies text alignment ðŸ ˆ|--//
        display: -webkit-box; //--|ðŸ Š Enables multi-line truncation ðŸ ˆ|--//
        -webkit-line-clamp: 4; //--|ðŸ Š Limits the number of lines  ðŸ ˆ|--//
        text-overflow: ellipsis; //--|ðŸ Š Adds ellipsis for overflow ðŸ ˆ|--//
        -webkit-box-orient: vertical; //--|ðŸ Š Vertical Text ðŸ ˆ|--//
        filter: drop-shadow(black 0 0 1rem); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      }
    }
    div[class*='icon'] {
      z-index: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      grid-area: 1 / 2 / 3 / 3;
      img {
        width: 50%;
        height: 50%;
        opacity: 0.25;
        mask-size: contain;
        mask-position: center;
        mask-repeat: no-repeat;
        background: Palettes.$indexLand-light;
      }
    }
  }
  div[class*='inputs'] {
    display: inherit; //--|ðŸ ˆ display: grid; ðŸ ˆ|--//
    grid-template: 8rem / 2rem 1fr 2rem; //--|ðŸ Š rows / columns ðŸ ˆ|--//
    fieldset[class*='code'] {
      grid-area: 1 / 2 / 2 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
    }
  }
  div[class*='footer'] {
    display: inherit; //--|ðŸ Š display: grid; ðŸ ˆ|--//
    grid-area: 3 / 1 / 4 / 2; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
    grid-template: 1fr 6rem / repeat(2, 1fr); //--|ðŸ Š rows / columns ðŸ ˆ|--//
    menu[class*='action'] {
      display: inherit; //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-area: 1 / 1 / 2 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      grid-template: 1rem auto 1fr / 2rem 1fr 2rem; //--|ðŸ Š rows / columns ðŸ ˆ|--//

      button[class*='verify'] {
        grid-area: 2 / 2 / 3 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
        filter: drop-shadow(
          rgba(Palettes.$indexLand-dark, 1) 0 0 8px
        ); //--|ðŸ ˆ drop-shadow( <color> <x-axis> <y-axis> <blur> ) ðŸ ˆ|--//
      }
    }
  }

  @media screen and (max-height: 768px) {
  }
  @media screen and (max-height: 360px) {
  }
}
@mixin mobile-portrait {
  //--|ðŸ Š display: grid; ðŸ ˆ|--//
  @media screen and (max-width: 768px) {
  }
  @media screen and (max-width: 360px) {
  }
}

form[class*='verify'] {
  display: grid;
  border-radius: 1rem;
  background: rgba(Palettes.$indexLand-dark, 0.75);
  box-shadow: 0 0 8rem rgba(Palettes.$indexLand-medium, 0.75); //--|ðŸ ˆ <x-axis> | <y-axis> | <blur> | <color> ðŸ ˆ|--//
  box-shadow: inset 0 0 8px rgba(Palettes.$indexLand-light, 1);
  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (orientation: landscape) {
    @include desktop-landscape();
  }
  //--|ðŸ ‹ Mobile (Portrait) ðŸ ‹|--//
  @media screen and (orientation: portrait) {
    @include mobile-portrait();
  }
}
