//--|ðŸ Š Fieldset.code.scss ðŸ ˆ|--//
@use '../../../designs/Palettes.scss';

@mixin desktop-landscape {
  input[class*='digit'] {
    width: 100%;
    grid-row: 1 / 2;
    font-size: 4rem;
    text-align: center;
    &::placeholder {
      font-weight: bold;
    }
    & {
      .digit-1 {
        grid-column: 1 / 2; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
      .digit-2 {
        grid-column: 2 / 3; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
      .digit-3 {
        grid-column: 3 / 4; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
      .digit-4 {
        grid-column: 4 / 5; //--|ðŸ Š row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
    }
  }
}
@mixin mobile-portrait {
}
fieldset[class*='code'] {
  display: grid;
  column-gap: 1rem;
  grid-template: 8rem / repeat(4, 1fr); //--|ðŸ Š rows / columns ðŸ ˆ|--//

  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (orientation: landscape) {
    @include desktop-landscape();
  }
  //--|ðŸ ‹ Mobile (Portrait) ðŸ ‹|--//
  @media screen and (orientation: portrait) {
    @include mobile-portrait();
  }
}
